import React, { useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Button, Input } from 'antd';
import { Form, Field } from 'react-final-form';
import { FORM_ERROR } from 'final-form';

import FieldWithValidation from 'components/FieldWithValidation';

import { isEmail } from 'utils';

import { ValidationErrorsObject } from 'types';
import { useTypedDispatch, useTypedSelector } from 'store';
import { logIn, LoginValues } from 'store/user/thunkActions';
import { initPeer } from 'store/core/thunkActions';

const Login = () => {
  const history = useHistory();
  const dispatch = useTypedDispatch();

  const { isAuth } = useTypedSelector((state) => state.user);

  const onSubmit = async (values: LoginValues) => {
    try {
      await dispatch(logIn({ email: values.email, password: values.password }));
      dispatch(initPeer());

      history.push('/chat');
    } catch (e) {
      return { [FORM_ERROR]: e.message };
    }
  };

  useEffect(() => {
    if (isAuth) {
      history.push('/');
    }
  }, []);

  return (
    <div className="auth-form">
      <Form
        onSubmit={onSubmit}
        validate={validate}
        render={({ handleSubmit, submitError, submitting }) => (
          <form className="auth-form auth-form--login" onSubmit={handleSubmit}>
            <div className="auth-form__title">–í—Ö–æ–¥</div>

            {submitError && <div className="auth-form__error">{submitError}</div>}

            <div className="auth-form__fields">
              <Field name="email">
                {({ input, meta }) => (
                  <FieldWithValidation meta={meta} errorText={meta.error}>
                    <Input {...input} placeholder="Email" />
                  </FieldWithValidation>
                )}
              </Field>
              <Field name="password">
                {({ input, meta }) => (
                  <FieldWithValidation meta={meta} errorText={meta.error}>
                    <Input.Password {...input} placeholder="–ü–∞—Ä–æ–ª—å" />
                  </FieldWithValidation>
                )}
              </Field>
            </div>

            {/* <Link to="/forgot-password" className="auth-form__forgot">
                  –ó–∞–±—ã–ª–∏ –ø–∞—Ä–æ–ª—å?
                </Link> */}
            <Button
              className="auth-form__button"
              type="primary"
              htmlType="submit"
              loading={submitting}
            >
              –í–æ–π—Ç–∏
            </Button>
            <div className="auth-form__footer-text">
              –í—Å–µ –µ—â—ë –Ω–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞? <Link to="/signup">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</Link>
            </div>
          </form>
        )}
      />
    </div>
  );
};

type FormState = {
  email: string;
  password: string;
};

const validate = (values: Partial<FormState>) => {
  const errors: ValidationErrorsObject<FormState> = {};

  if (!values.email) {
    errors.email = '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ';
  }
  if (values.email && !isEmail(values.email)) {
    errors.email = '–í–≤–µ–¥–∏—Ç–µ email –ø—Ä–∞–≤–∏–ª—å–Ω–æ';
  }
  if (!values.password || values.password.trim().length <= 6) {
    errors.password = '–í–≤–µ–¥–∏—Ç–µ –Ω–µ –º–µ–Ω–µ–µ 6 —Å–∏–º–≤–æ–ª–æ–≤';
  }

  return errors;
};

export default Login;
