import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Form, Field } from 'react-final-form';
import { Button, Input, message } from 'antd';
import { FORM_ERROR } from 'final-form';
import clsx from 'clsx';

import FieldWithValidation from 'components/FieldWithValidation';

import { isEmail } from 'utils';
import apiClient from 'utils/apiClient';

import { ValidationErrorsObject } from 'types';

type Props = {
  title?: string;
  isFromAdmin?: boolean;
};

const Signup: React.FC<Props> = ({
  title = '–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è',
  isFromAdmin = false,
}) => {
  const history = useHistory();

  const [role, setRole] = useState<'client' | 'operator' | 'admin'>('client');

  const onSubmit = async (values: {
    email: string;
    password: string;
    firstName: string;
    lastName: string;
  }) => {
    const data = {
      email: values.email,
      password: values.password,
      firstName: values.firstName,
      lastName: values.lastName,
      role,
    };

    try {
      await apiClient.service('users').create(data);
      message.success('–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É—Å–ø–µ—à–Ω–æ —Å–æ–∑–¥–∞–Ω');
      if (!isFromAdmin) {
        history.push('/login');
      }
    } catch (e) {
      console.error('signup err', e);
      return { [FORM_ERROR]: e.message };
    }
  };

  return (
    <Form
      onSubmit={onSubmit}
      validate={validate}
      render={({ handleSubmit, submitError, submitting }) => (
        <form className="auth-form auth-form--signup" onSubmit={handleSubmit}>
          <div className="auth-form__title">{title}</div>

          {submitError && <div className="auth-form__error">{submitError}</div>}

          {isFromAdmin && (
            <div className="auth-form__role">
              <div
                className={clsx('auth-form__role-item', {
                  'auth-form__role-item--active': role === 'client',
                })}
                onClick={() => setRole('client')}
              >
                –ö–ª–∏–µ–Ω—Ç
              </div>
              <div
                className={clsx('auth-form__role-item', {
                  'auth-form__role-item--active': role === 'operator',
                })}
                onClick={() => setRole('operator')}
              >
                –û–ø–µ—Ä–∞—Ç–æ—Ä
              </div>
              <div
                className={clsx('auth-form__role-item', {
                  'auth-form__role-item--active': role === 'admin',
                })}
                onClick={() => setRole('admin')}
              >
                –ê–¥–º–∏–Ω
              </div>
            </div>
          )}

          <div className="auth-form__fields">
            <Field name="firstName">
              {({ input, meta }) => (
                <FieldWithValidation meta={meta} errorText={meta.error}>
                  <Input {...input} placeholder="–ò–º—è" />
                </FieldWithValidation>
              )}
            </Field>
            <Field name="lastName">
              {({ input, meta }) => (
                <FieldWithValidation meta={meta} errorText={meta.error}>
                  <Input {...input} placeholder="–§–∞–º–∏–ª–∏—è" />
                </FieldWithValidation>
              )}
            </Field>
            <Field name="email">
              {({ input, meta }) => (
                <FieldWithValidation meta={meta} errorText={meta.error}>
                  <Input {...input} placeholder="Email" />
                </FieldWithValidation>
              )}
            </Field>
            <Field name="password">
              {({ input, meta }) => (
                <FieldWithValidation meta={meta} errorText={meta.error}>
                  <Input.Password {...input} placeholder="–ü–∞—Ä–æ–ª—å" />
                </FieldWithValidation>
              )}
            </Field>
          </div>

          <Button
            className="auth-form__button"
            type="primary"
            htmlType="submit"
            loading={submitting}
          >
            {isFromAdmin ? '–î–æ–±–∞–≤–∏—Ç—å' : '–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è'}
          </Button>
          {!isFromAdmin && (
            <div className="auth-form__footer-text">
              –ï—Å—Ç—å –∞–∫–∫–∞—É–Ω—Ç? <Link to="/login">–í–æ–π—Ç–∏</Link>
            </div>
          )}
        </form>
      )}
    />
  );
};

type FormState = {
  email: string;
  password: string;
  firstName: string;
  lastName: string;
};

const validate = (values: Partial<FormState>) => {
  const errors: ValidationErrorsObject<FormState> = {};

  if (!values.email) {
    errors.email = '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ';
  }
  if (values.email && !isEmail(values.email)) {
    errors.email = '–í–≤–µ–¥–∏—Ç–µ email –ø—Ä–∞–≤–∏–ª—å–Ω–æ';
  }
  if (!values.password || values.password.trim().length < 3) {
    errors.password = '–í–≤–µ–¥–∏—Ç–µ –Ω–µ –º–µ–Ω–µ–µ 6 —Å–∏–º–≤–æ–ª–æ–≤';
  }
  if (!values.firstName) {
    errors.firstName = '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ';
  }
  if (!values.lastName) {
    errors.lastName = '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ';
  }

  return errors;
};

export default Signup;
